.text-field{position:relative;display:block}.text-field__title{margin-bottom:5px}.text-field__caption{position:absolute;top:0;right:0;font-size:12px;line-height:15px;text-transform:uppercase;white-space:nowrap;color:rgba(31,32,65,.5)}.text-field__field{display:flex;align-items:center;width:100%;height:44px;padding:0 15px;line-height:40px;color:rgba(31,32,65,.75);border:1px solid rgba(31,32,65,.25);border-radius:4px}.text-field__field::-webkit-input-placeholder{opacity:1;color:rgba(31,32,65,.25);-webkit-transition:opacity .2s ease-in-out;transition:opacity .2s ease-in-out}.text-field__field::-moz-placeholder{opacity:1;color:rgba(31,32,65,.25);-moz-transition:opacity .2s ease-in-out;transition:opacity .2s ease-in-out}.text-field__field:focus{border-color:rgba(31,32,65,.5);transition:all .2s ease-in-out}.text-field__field:focus::-webkit-input-placeholder{opacity:0}.text-field__field:focus::-moz-placeholder{opacity:0}.text-field.with-arrow:after{color:#bc9cff;content:"arrow_forward";position:absolute;right:0;bottom:0;display:flex;justify-content:center;align-items:center;width:44px;height:44px;font-size:24px;line-height:24px;font-family:material icons,sans-serif}