.dd-dates{position:relative}.dd-dates__fields{display:flex;justify-content:space-between}.dd-dates__end,.dd-dates__start{width:calc(50% - 10px)}.dd-dates__title{margin-bottom:5px;cursor:default}.dd-dates__field{position:relative;cursor:default;width:100%;padding:0 15px;line-height:40px;color:rgba(31,32,65,.75);border:1px solid rgba(31,32,65,.25);border-radius:4px}.dd-dates__field,.dd-dates__field:after{display:flex;align-items:center;height:44px}.dd-dates__field:after{content:"expand_more";position:absolute;right:0;justify-content:center;width:44px;font-size:24px;font-family:material icons,sans-serif;color:rgba(31,32,65,.5)}.dd-dates__calendar{position:absolute;top:calc(100% + 5px);right:0;left:0}