.calendar{z-index:20;display:flex;flex-direction:column;overflow:hidden;visibility:hidden;width:320px;padding:20px;opacity:0;background:#fff;border:1px solid rgba(31,32,65,.25);border-radius:4px}.calendar.is-open{visibility:visible;opacity:1;transition:all .2s ease-in-out}.calendar__month{display:flex;justify-content:space-between;margin-bottom:30px}.calendar__month-change{margin:-10px;padding:10px;font-size:24px;color:#bc9cff;cursor:pointer;user-select:none}.calendar__month-change:hover{color:rgba(31,32,65,.5);transition:all .2s ease-in-out}.calendar__days-of-week,.calendar__week{display:flex;font-size:12px;line-height:15px}.calendar__date,.calendar__day-of-week{display:flex;justify-content:center;align-items:center;width:14.28571%;min-width:40px;height:40px}.calendar__day-of-week{font-weight:700;color:#bc9cff}.calendar__date{position:relative;color:rgba(31,32,65,.25);cursor:pointer;user-select:none}.calendar__date.is-selected-month{color:rgba(31,32,65,.5)}.calendar__date.is-gap-date:after{right:0;left:0;content:"";position:absolute;z-index:-2;top:0;bottom:0;background:linear-gradient(180deg,#bc9cff,#8ba4f9);opacity:.25}.calendar__date.is-end-date,.calendar__date.is-start-date,.calendar__date.is-today{font-weight:700;color:#fff}.calendar__date.is-end-date:before,.calendar__date.is-start-date:before,.calendar__date.is-today:before{content:"";position:absolute;z-index:-1;top:0;right:0;bottom:0;left:0;border-radius:20px}.calendar__date.is-today:before{background:linear-gradient(180deg,#6fcf97,#66d2ea)!important}.calendar__date.is-end-date:before,.calendar__date.is-start-date:before{background:linear-gradient(180deg,#bc9cff,#8ba4f9)}.calendar__date.is-start-date.with-gap-background:after{right:0;left:50%}.calendar__date.is-end-date.with-gap-background:after,.calendar__date.is-start-date.with-gap-background:after{content:"";position:absolute;z-index:-2;top:0;bottom:0;background:linear-gradient(180deg,#bc9cff,#8ba4f9);opacity:.25}.calendar__date.is-end-date.with-gap-background:after{right:50%;left:0}.calendar__buttons{display:flex;justify-content:space-between;flex-direction:row-reverse;margin-top:20px}.calendar__buttons .is-hidden{display:none}