.like{position:relative;display:inline-block;width:40px;height:20px;border:1px solid rgba(31,32,65,.25);border-radius:10px;background:#fff;cursor:pointer;user-select:none}.like__symbol{position:absolute;top:0;left:0;display:flex;justify-content:center;align-items:center;width:20px;height:18px}.like__symbol:before{content:"favorite_border";line-height:9px}.like__count,.like__symbol:before{font-size:10px;color:rgba(31,32,65,.25)}.like__count{position:absolute;top:0;right:0;width:26px;height:19px;line-height:19px;text-align:center}.like:hover{opacity:.5;transition:all .2s ease-in-out}.like.is-active{border-color:#bc9cff}.like.is-active .like__symbol:before{content:"favorite";color:#bc9cff}.like.is-active .like__count{color:#bc9cff}