.dd-dates-filter{position:relative}.dd-dates-filter__title{margin-bottom:5px;cursor:default}.dd-dates-filter__field{position:relative;cursor:default;display:flex;align-items:center;width:100%;height:44px;padding:0 15px;line-height:40px;color:rgba(31,32,65,.75);border:1px solid rgba(31,32,65,.25);border-radius:4px}.dd-dates-filter__field:after{content:"expand_more";position:absolute;right:0;display:flex;justify-content:center;align-items:center;width:44px;height:44px;font-size:24px;font-family:material icons,sans-serif;color:rgba(31,32,65,.5)}.dd-dates-filter__calendar{position:absolute;top:calc(100% + 5px);right:0;left:0}