.checkbox{display:inline-flex;align-items:flex-start}.checkbox__field{margin-right:10px;display:flex;justify-content:center;align-items:center;width:20px;height:20px;border:1px solid rgba(31,32,65,.25);border-radius:4px;padding:0 0 2px 1px;background:#fff}.checkbox__field:checked{border-color:#bc9cff}.checkbox__field:checked:before{content:"";position:absolute;width:6px;height:8px;background:linear-gradient(180deg,#bc9cff,#8ba4f9);transform:rotate(45deg)}.checkbox__field:checked:after{content:"";position:absolute;width:6px;height:8px;background:#fff;transform:rotate(45deg) translate(-2px,-2px)}.checkbox__title{flex:1;margin:2px 0 0;font-size:14px;line-height:18px;color:rgba(31,32,65,.5)}