.radio{display:inline-flex;color:rgba(31,32,65,.5)}.radio__field{position:relative;top:2px;display:flex;justify-content:center;align-items:center;width:20px;height:20px;margin-right:10px;border:1px solid rgba(31,32,65,.25);border-radius:10px;background:#fff}.radio__field:checked{border-color:#bc9cff}.radio__field:checked:before{content:"";position:absolute;width:12px;height:12px;border-radius:12px;background:linear-gradient(180deg,#bc9cff,#8ba4f9)}.radio__field:checked+.radio__title{color:rgba(31,32,65,.75)}