body{display:flex;flex-direction:column;background:#fff;font-size:14px;line-height:1.7;color:rgba(31,32,65,.75);font-family:montserrat,-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen,Ubuntu,Cantarell,Fira Sans,Droid Sans,Helvetica Neue,sans-serif}.wrapper{min-width:320px;max-width:1920px;margin:0 auto;padding:0 150px}.heading{font-weight:700;color:#1f2041}.heading--level1{font-size:24px;line-height:30px}.heading--level1,.heading--level2{font-family:open sans,-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen,Ubuntu,Cantarell,Fira Sans,Droid Sans,Helvetica Neue,sans-serif}.heading--level2{font-size:19px;line-height:24px}.heading--level3{font-size:12px;line-height:15px;text-transform:uppercase}.heading--level4{font-size:14px;line-height:18px;font-weight:700}.paragraph{font-size:14px;line-height:24px;color:rgba(31,32,65,.75)}.list>*{position:relative;margin:0 0 10px 20px}.list>:before{content:"";position:absolute;top:7px;left:-20px;width:10px;height:10px;border-radius:5px;background:rgba(31,32,65,.25)}.btn{position:relative;display:inline-flex;align-items:center;font-size:12px;line-height:15px;font-weight:700;text-transform:uppercase;text-align:center;cursor:pointer;user-select:none}.btn--action,.btn--primary,.btn--secondary{height:44px;border:2px solid transparent;border-radius:22px;padding:1px 18px 0}.btn--action:before,.btn--primary:before,.btn--secondary:before{content:"";position:absolute;z-index:-1;top:0;right:0;bottom:0;left:0;margin:-2px;border-radius:inherit;background:linear-gradient(180deg,#bc9cff,#8ba4f9)}.btn--action:hover:before,.btn--primary:hover:before,.btn--secondary:hover:before{opacity:.5;transition:all .2s ease-in-out}.btn--primary{width:auto;color:#fff}.btn--secondary{background:#fff;background-clip:padding-box;color:#bc9cff}.btn--inline{margin:-5px;padding:5px;color:#bc9cff}.btn--inline:hover{color:rgba(31,32,65,.5);transition:all .2s ease-in-out}.btn--action{position:relative;display:flex;justify-content:center;align-items:center;width:100%;text-align:center;color:#fff}.btn--action.with-arrow:after{color:#fff}.icon{display:inline-block;font-size:1em;line-height:1;font-family:material icons,sans-serif;text-transform:none;letter-spacing:normal;word-wrap:normal;white-space:nowrap;user-select:none}