.pagination__container{display:inline-flex;flex-direction:column}.pagination__pages{display:flex;margin-bottom:10px;user-select:none}.pagination__page{display:flex;justify-content:center;align-items:center;width:40px;height:40px;font-size:12px;line-height:15px;color:rgba(31,32,65,.5)}.pagination__page:not(.pagination__page--gap){cursor:pointer}.pagination__page--current,.pagination__page--next,.pagination__page--previous{border-radius:20px;font-weight:700;color:#fff}.pagination__page--current:hover,.pagination__page--next:hover,.pagination__page--previous:hover{opacity:.5;transition:.2s ease-in-out}.pagination__page--current{background:linear-gradient(180deg,#bc9cff,#8ba4f9)}.pagination__page--next,.pagination__page--previous{background:linear-gradient(180deg,#6fcf97,#66d2ea)}.pagination__page--next:before,.pagination__page--previous:before{font-size:24px;line-height:20px;font-family:material icons,sans-serif;font-weight:400}.pagination__page--previous:before{content:"arrow_back"}.pagination__page--next:before{content:"arrow_forward"}.pagination__caption{text-align:center}