.range__title-and-caption{display:flex;justify-content:space-between;margin-bottom:11px}.range__caption{font-size:12px;line-height:15px;text-align:right;text-transform:uppercase;color:rgba(31,32,65,.5)}.range__slider{position:relative;display:flex;align-items:center;height:24px}.range__slider:before{content:"";width:100%;height:6px;border:1px solid rgba(31,32,65,.25);border-radius:3px;background:#fff}.range__thumb{position:absolute;z-index:5;display:flex;justify-content:center;align-items:center;width:20px;height:20px;border-radius:10px;cursor:pointer;transform-origin:center;transform:translateX(-10px)}.range__thumb:before{content:"";box-sizing:initial;width:12px;height:12px;border:2px solid #fff;border-radius:7px}.range__gap,.range__thumb:before{background:linear-gradient(180deg,#6fcf97,#66d2ea)}.range__gap{position:absolute;z-index:4;height:6px}