.checkbox-rich{display:inline-flex;align-items:flex-start}.checkbox-rich__field{margin-right:10px;display:flex;justify-content:center;align-items:center;width:20px;height:20px;border:1px solid rgba(31,32,65,.25);border-radius:4px;padding:0 0 2px 1px;background:#fff}.checkbox-rich__field:checked{border-color:#bc9cff}.checkbox-rich__field:checked:before{content:"";position:absolute;width:6px;height:8px;background:linear-gradient(180deg,#bc9cff,#8ba4f9);transform:rotate(45deg)}.checkbox-rich__field:checked:after{content:"";position:absolute;width:6px;height:8px;background:#fff;transform:rotate(45deg) translate(-2px,-2px)}.checkbox-rich__title-and-caption{flex:1}.checkbox-rich__title{margin:2px 0 5px;font-size:14px;line-height:18px;color:rgba(31,32,65,.75)}.checkbox-rich__caption{font-size:12px;line-height:14px;color:rgba(31,32,65,.5)}