.toggle{position:relative;display:inline-flex}.toggle__indicator{position:absolute;top:2px;left:0;display:flex;align-items:center;width:40px;height:20px;border:1px solid rgba(31,32,65,.25);border-radius:10px}.toggle__indicator:before{content:"";width:12px;height:12px;border-radius:6px;background:rgba(31,32,65,.25);transition:transform .2s ease-in-out;transform:translateX(4px)}.toggle__indicator:checked{border-color:#bc9cff}.toggle__indicator:checked:before{background:linear-gradient(180deg,#bc9cff,#8ba4f9);transition:all .2s ease-in-out;transform:translateX(24px)}.toggle__title{margin-left:50px;font-size:14px;line-height:24px}